
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: "FileLogin",
components: {
XHeader
},
data() {
return {
pwd: "",
disable: true,
errorTips: false
};
},
watch: {
pwd(val) {
this.changetext();
}
},
methods: {
testpwd(str) {
var regUpper = /[A-Z]/;
var regLower = /[a-z]/;
var regStr = /[0-9]/;
var complex = 0;
if (regLower.test(str)) {
++complex;
}
if (regUpper.test(str)) {
++complex;
}
if (regStr.test(str)) {
++complex;
}
if (complex < 3 || str.length < 6) {
return false;
} else {
return true;
}
},
changetext() {
this.errorTips = !this.testpwd(this.pwd);
this.disable = this.pwd.length >= 6 ? false : true;
},
signIn() {
if (this.disable) {
return false;
}
this.$ajax
.post("", {
"MSH.1": "user",
"MSH.2": "ckPassword",
"token": this.$root.loginInfo.token,
"password": this.pwd,
})
.then(data => {
if (typeof data != "undefined" && typeof data.status != "undefined") {
if (data.status == 1) {
this.$router.push({name: 'changephonenumberVerify', params: {PWD: this.pwd}});
} else if (data.status == 2) {
this.$vux.toast.text("密码错误", "bottom");
}
}
})
.catch(error => {
console.log(error);
});
},
gotoLoginView() {
if (this.$root.phoneType == "ios") {
window.webkit.messageHandlers.mutual.postMessage({ type: "1" });
} else {
android: AndroidFun.mutual("1");
}
}
}
};



